<template>
	<div class="app-container">
		<p>
			<el-col :span="8">
				选择月份：
				<el-date-picker v-model="month" filters type="month" format="yyyy-MM" @change="dateChange" placeholder="选择月" :clearable="false">
				</el-date-picker>
			</el-col>
		</p>
		<el-table :data="tableData" border style="width: 100%"  v-loading="isLoad">
			<el-table-column prop="time" label="支出年月" width="150" align="center">
			</el-table-column>
			<el-table-column prop="contractNumber" label="合同编号" width="180" align="center">
			</el-table-column>
			<el-table-column prop="packagePrice" label="流量包收入(元)" width="150" align="center">
			</el-table-column>
			<el-table-column prop="ctRatio" label="佣金比例" width="150" align="center">
			</el-table-column>
			<el-table-column prop="platformCommision" label="佣金支出" align="center">
			</el-table-column>
		</el-table>
		<div style="text-align:center;margin: 20px auto;">
			<el-pagination style="display: inline-block" :page-size="10" @current-change="pageChange" :current-page.sync="currentPage" layout="prev, pager, next, jumper" :total="tableCount">
			</el-pagination>
		</div>
	</div>
</template>

<script>
/* eslint-disable quotes,indent,keyword-spacing,spaced-comment,no-var */
import {
	listRechargeCommision
} from 'api/admin/financialSettlement';
	import { backCN } from 'utils/index';
export default {
	data() {
		return {
			tableData: [],
			month: '2017-06',
			currentMonth: '201706',
			currentPage: 1,
			tableCount: 0,
			num: 10,
			isLoad:true
		}
	},
	watch: {},
	mounted() {
	this.getListRechargeCommision('201706', 1, this.num)
	},
	methods: {
		dateChange(val) {
			this.currentMonth= val.split('-').join('')
			this.getListRechargeCommision(this.currentMonth, 1, this.num)
		},
		pageChange(page) {
     	this.getListRechargeCommision(this.currentMonth, page, this.num)
		},
		getListRechargeCommision(month,page,num){
			this.isLoad=true
              listRechargeCommision({month,page,num}).then(res=>{
                  this.isLoad=false
                  console.log(res)
				  		if (res.msg == 0) {
						this.tableData = res.commisionList
						page === 1 ? this.tableCount = res.count : ''
					}else {
						this.tableData = []
						this.tableCount = 0
					}
					(res.msg != 0 && res.msg != 1) ? this.$message.error(backCN(res.code)) : ''
			  })
		}
	}
};
</script>

<style scoped>
.app-container {
	background: #f5f5f5;
}

.label-colum {
	font-size: 14px;
	color: #fff;
	font-weight: 100;
}
</style>


